import complianceContent from "@/components/complianceContent";
export default {
  async asyncData({ $axios, i18n, params }) {
    params.lang = i18n.locale == "en" ? "en" : i18n.locale;
    const { data } = (await $axios.get(`compliance`, { params })).data;

    return { data };
  },
  components: {
    complianceContent,
  },
};
